/* Dropdown animation for the product flyout menu */
#product-dropdown {
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
  opacity: 0;
  transform: translateY(10px);
}
#product-dropdown:not(.hidden) {
  opacity: 1;
  transform: translateY(0);
}

/* Mobile menu animation */
#mobile-menu .fixed.inset-y-0 {
  transition: opacity 0.3s ease-in-out;
}
#mobile-menu .fixed.inset-y-0:not(.hidden) {
  animation: slide-in 0.3s forwards;
}

@keyframes slide-in {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
/* Rotate animation for dropdown chevron */
.rotate-180 {
  transform: rotate(180deg);
  transition: transform 0.2s ease;
}

#mobile-menu-backdrop {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

#mobile-menu:not(.hidden) #mobile-menu-backdrop {
  opacity: 1;
}
#mobile-menu:not(.hidden) .fixed.inset-y-0.right-0 {
  box-shadow: -5px 0 15px rgba(0, 0, 0, 0.1);
}

#menu-toggle:focus, #menu-toggle:hover {
  outline: none;
  color: var(--arabic-green);
}

.debug-mobile-menu-visible #mobile-menu {
  display: block !important;
}
